*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --main-bg-color: #343a40;
  --main-txt-color: #d7d1de;
  --main-color: #371b58;
  --main-color-light: #5b4b8a;
}

html {
  font-size: 62.5%;
}

body {
  background-color: var(--main-bg-color);
  font-family: "Permanent Marker", cursive;
  font-weight: 400;
  line-height: 1;
  color: var(--main-txt-color);
}

.margin_small {
  margin-top: 5rem;
}

.margin_medium {
  margin-top: 8rem;
}

/* COMPONENTS */

.main {
  max-width: 120rem;
  margin: 5rem auto 2rem;
  border: 0.5rem solid var(--main-txt-color);

  background: rgb(33, 37, 41);
  background: linear-gradient(
    125deg,
    rgba(33, 37, 41, 1) 0%,
    rgba(33, 37, 41, 1) 18%,
    rgba(33, 37, 41, 1) 100%
  );
}

.hero__main {
  height: 80vh;
  position: relative;
  z-index: 500;
}

.hero__main_header {
  padding: 15rem 10rem 0;
}

.header {
  font-size: 6.4rem;
}

.header__secondary {
  font-size: 4rem;
}

.header__assist {
  font-size: 2.3rem;
}

.hero__bg-video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1000;
  opacity: 0.35;
  clip-path: polygon(42% 0, 100% 0%, 100% 100%, 0% 100%);
}

.video_content {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.btn,
.btn:visited,
.btn:link {
  display: inline-block;
  padding: 1.4rem 2.8rem;
  border-radius: 5rem;
  border: 0.3rem solid var(--main-txt-color);
  text-decoration: none;
  transition: all 0.3s;
}

.btn:hover {
  transform: scale(1.1) translateY(-5px);
  background-color: var(--main-color-light);
  box-shadow: 0 20px 10px rgba(255, 255, 255, 0.1);
}

.btn:active {
  background-color: #371b58;
  transform: scale(1) translateY(0);
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.1);
}

.hero__btn {
  font-family: "Permanent Marker", cursive;
  background-color: var(--main-color);
  font-size: 2.3rem;
  color: var(--main-txt-color);
  /* background-color: var(--main-bg-color); */
}

#schedules {
  /* height: 250vh; */
  padding: 3rem 10rem 0;
}

.testimonials {
  max-width: 80rem;
  margin: 0 auto;
}

.figure {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* height: 16.5rem; */
}

.testimonial:not(:last-child) {
  position: relative;
  padding: 1rem 0;
  border-bottom: 0.1rem solid var(--main-txt-color);
}

.testimonial__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  font-size: 4rem;
  z-index: 20;
}

/* .testimonial1::after {
  content: "";
  background: url("./imgs/blob.svg");
  height: 50rem;
  width: 50rem;
  position: absolute;
  top: -120px;
  right: 100px;
  clip-path: circle(40%);
  z-index: 10;
} */

.testimonial__description * {
  z-index: 50;
}

.testimonial_img {
  width: 25rem;
}

#callToAction {
  background: rgb(33, 37, 41);
  /* height: 50vh; */
  padding: 3rem 10rem 0;
  margin: 5rem 0;
}

.cta__grid {
  border-top: 1px solid var(--main-txt-color);
  border-right: 1px solid var(--main-txt-color);
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(4, max-content);
  gap: 10px 10px;
  margin-bottom: 5rem;
}

.cta__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.cta__item-1 {
  grid-column: 1/2;
}

.cta__item-2 {
  grid-column: 2/3;
}

.cta__item-3 {
  grid-column: 3/4;
}

.cta__item-4 {
  grid-column: 4/5;
}

.cta__item-5 {
  grid-column: 5/-1;
}

.cta__item-6 {
  grid-column: 5/-1;
  grid-row: 2/5;
}

/* .cta__item-7 {
  grid-column: 5/-1;
  grid-row: 4/6;
} */

.cta__header {
  grid-column: 1/5;
  grid-row: 2/3;
  padding-top: 5rem;
  padding-left: 10rem;
}

.cta__form__container {
  grid-column: 1/5;
  grid-row: 3/4;
  /* padding-top: 5rem; */
  padding-left: 10rem;
  max-width: 45rem;
  margin-top: 3rem;
}

.cta__form {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}

.input {
  width: 40rem;
  padding: 1rem 2rem;
  box-shadow: 0 20px 10px rgba(255, 255, 255, 0.1);
  font-family: inherit;
  font-size: 2.3rem;
  outline: none;
  transition: all 0.2s;
}

.input:focus {
  background-color: var(--main-txt-color);
  border: 0.3rem solid var(--main-color);
  transform: scale(1.01) translateY(-10px);
  box-shadow: 0 30px 20px rgba(255, 255, 255, 0.1);
}

#footer {
  margin: 7rem 0 2rem 0;
  display: flex;
  justify-content: center;
}

/* BREAKPOINT 1200PX  */
@media screen and (max-width: 75em) {
  .main {
    margin: 0;
  }
}

/* BREAKPOINT 1000PX  */
@media screen and (max-width: 62.5em) {
  html {
    font-size: 50%;
  }

  .testimonial3 figure {
    margin-top: 1.5rem;
  }
}

/* BREAKPOINT 700PX  */
@media screen and (max-width: 44em) {
  html {
    font-size: 43.5%;
  }

  .cta__form {
    align-items: center;
  }

  .cta__grid {
    border-right: none;
  }

  .cta__item-6 {
    display: none;
  }

  .cta__header {
    grid-column: 2/5;
    grid-row: 2/3;
    padding-left: 0;
    text-align: center;
  }

  .cta__form__container {
    grid-column: 2/5;
    grid-row: 3/4;
    padding-left: 0;
  }
}

/* BREAKPOINT 700PX  */
@media screen and (max-width: 37.5em) {
  .hero__main {
    text-align: center;
  }
  .figure {
    flex-direction: column;
    gap: 2rem;
  }

  #schedules {
    padding: 2rem 0;
    text-align: center;
  }

  #callToAction {
    margin: 0;
    padding: 0;
  }

  .cta__grid {
    gap: 0;
  }

  .cta__item-1 {
    grid-column: 1/6;
    height: 15rem;
  }

  .cta__item-2 {
    display: none;
  }

  .cta__item-3 {
    display: none;
  }
  .cta__item-4 {
    display: none;
  }
  .cta__item-5 {
    display: none;
  }
  .cta__item-6 {
    display: none;
  }
}
